<template>
  <div class="about">
    <h1>{{id ? '编辑' : '新建'}}音乐</h1>
    <el-form label-width="100px" @submit.native.prevent="save">
      <el-form-item label="歌名">
        <el-input placeholder="歌曲名称" v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="歌手">
        <el-input placeholder="歌手" v-model="model.name"></el-input>
      </el-form-item>

      <el-form-item label="图标">
        <el-upload
        id="audioUpload"
        class="avatar-uploader"
        :action="uploadUrl"
        :headers="gteAuthHeaders()"
        :multiple="false"
        :limit="1"
        :show-file-list="true"
        :auto-upload="true"
        :on-success="handleAvatarSuccess">
          <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
          <div slot="tip" class="el-upload__tip" style="margin-bottom: 10px">
              只能上传mp3或wav文件
          </div>
       </el-upload>
      </el-form-item>
      
      <el-form-item label="图标">
        <el-upload
        class="avatar-uploader"
        :action="uploadUrl"
        :headers="gteAuthHeaders()"
        :show-file-list="false"
        :on-success="afterUpload">
          <img v-if="model.icon" :src="model.icon" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      model: {}
    }
  },
  methods: {
    handleAvatarSuccess(file) {
      console.log(file)
      this.$set(this.model, 'audio', file.url)
                // 文件类型进行判断
                const isAudio = file.type === "audio/mp3" || file.type === "audio/wav";
                return isAudio
            },
             
    afterUpload (res) {
      console.log(res)
      this.$set(this.model, 'icon', res.url)
      // this.model.icon = res.url
    },
    async save () {
      let res
      if (this.id) {
        res = await this.$http.put(`rest/musics/${this.id}`, this.model)
        console.log(res)
      }else {
        res = await this.$http.post('rest/musics', this.model)
        console.log(res)
      }
      this.$router.push('/musics/list')
      this.$message({
        type: 'success',
        message: '保存成功'
      })
    },
    async fetch () {
      const res = await this.$http.get(`rest/musics/${this.id}`)
      this.model =res.data
    }
  },
  created () {
    this.id && this.fetch()
  }
}
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
