<template>
  <div class="about">
    <h1>{{id ? '编辑' : '新建'}}广告图</h1>
    <el-form label-width="100px" @submit.native.prevent="save">

      <el-form-item label="广告名">
        <el-input placeholder="广告名" v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="链接">
        <el-input placeholder="链接" v-model="model.url"></el-input>
      </el-form-item>
      <el-form-item label="广告图">
        <el-upload
        class="avatar-uploader"
        :action="uploadUrl"
        :headers="gteAuthHeaders()"
        :show-file-list="false"
        :on-success="afterUpload">
          <img v-if="model.image" :src="model.image" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data () {
    return {
      model: {}
    }
  },
  methods: {
    afterUpload (res) {
      // console.log(res)
      this.$set(this.model, 'image', res.url)
      // this.model.icon = res.url
    },
    async save () {
      let res
      if (this.id) {
        res = await this.$http.put(`rest/ads/${this.id}`, this.model)
        console.log(res)
      }else {
        res = await this.$http.post('rest/ads', this.model)
        console.log(res)
      }
      this.$router.push('/ads/list')
      this.$message({
        type: 'success',
        message: '保存成功'
      })
    },
    async fetch () {
      const res = await this.$http.get(`rest/ads/${this.id}`)
      this.model =res.data
    }
  },
  created () {
    this.id && this.fetch()
  }
}
</script>

