  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 920px;
  height: 480px;
  line-height: 480px;
  text-align: center;
}
.avatar {
  width: 920px;
  height: 480px;
  display: block;
}
</style>
