<template>
  <div class="about">
    <h1>广告图列表</h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID" width="220"></el-table-column>
      <el-table-column prop="name" label="广告名"></el-table-column>
      <el-table-column prop="url" label="链接"></el-table-column>
      <el-table-column prop="image" label="广告图">
        <template slot-scope="scope">
          <img :src="scope.row.image" style="height:4rem;">
        </template>
      </el-table-column>
      <el-table-column
      fixed="right"
      label="操作"
      width="180">
      <template slot-scope="scope">
        <el-button type="text" size="small"
        @click="$router.push(`/ads/edit/${scope.row._id}`)">编辑</el-button>
        <el-button type="text" size="small"
        @click="remove(scope.row)">删除</el-button>
      </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: []
    }
  },
  methods: {
    async fetch () {
      const res = await this.$http.get('rest/ads')
      this.items = res.data
    },
    async remove (row) {
      this.$confirm(`确定要删除"${row.name}"吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const res = await this.$http.delete(`rest/ads/${row._id}`)
          console.log(res)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        })
    }
  },
  created () {
    this.fetch()
  }
}
</script>
